.navLogo  {
  width: 15%;
  padding-left: 20px;
}

.navbar-brand {
  width: 300px;
}

.connect {
  font-size: 1em;
  padding: 0 2%;
  color: #123b1b;
  font-weight: bold;
}

.header {
  background-color:#000000;
  background-image: url(https://cdn.pixabay.com/photo/2018/05/28/11/42/dog-3435827_960_720.jpg);
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: right;
  height: 700px;
  width: 100%;
  color: #ffffff;
}

@media screen and (min-width: 600px) {
  .header {
    height: 800px;
  }
}

h1 {
  margin: 0%;
  padding: 15% 0 1% 10%;
  font-size: 3em;
}

p {
  margin: 0;
  padding: 0 0 1% 10%;
  font-size: 1.3em;
}

.app-name {
  font-variant: small-caps;
  font-size: 1.4em;
  font-weight: bold;
  color: #f5f2d7;

}

.call-to-action, .call-to-action2 {
  color: #e5f594;
  text-decoration: none;
  margin: 0;
  padding-left: 10%;
  font-size: 1.5em;
}

.call-to-action2 {
  color:  #00ff40;
  padding-left: 2%;
}

.api {
  height: 400px;
  width: 100%;
}

.footer {
  text-align: center;
  margin: 2%;
}

.copyright {
  font-size: .85em;
  margin: 2%;
  text-align: center;
  padding: 0;
}